.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 20px;
    color: white;
  
    .logo h1 {
      font-size: 1.8rem;
      margin: 0;
    }
  
    .nav-links {
      list-style-type: none;
      display: flex;
      gap: 20px;
  
      li {
        a {
          text-decoration: none;
          color: white;
          font-size: 1.2rem;
          transition: color 0.3s;
  
          &:hover {
            color: #f7f7f7;
          }
        }
      }
    }
  }
  